<template>
    <div class="card">
        <nav-bar></nav-bar>
        <el-form  class="card-info" >
            <el-form-item label="活动时间："label-width="200px">
                <el-date-picker
                        v-model="date"
                        type="daterange"
                        range-separator="至"
                        start-placeholder="开始日期"
                        end-placeholder="结束日期">
                </el-date-picker>
            </el-form-item>
            <el-form-item label="卡券有效期："label-width="200px">
                <el-date-picker
                        v-model="date"
                        type="daterange"
                        range-separator="至"
                        start-placeholder="开始日期"
                        end-placeholder="结束日期">
                </el-date-picker>
            </el-form-item>
            <el-form-item label="选择卡券："label-width="200px">
                <ul >
                    <li style="margin-top:40px" v-for="(item,index) in cards" :index="index">
                        <el-card class='mycard':body-style="{ padding: '0px' ,textAlign:'center'}">
                            <span>{{item}}</span>
                            <el-switch
                                    class="myswitch"
                                    v-model="value"
                                    active-color="#13ce66"
                                    inactive-color="#ff4949">
                            </el-switch>
                        </el-card>
                    </li>
                </ul>
                卡券发布后不可撤回
            </el-form-item>
            <el-form-item>
                <el-button size="small"  type="danger">确认发布</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    import navBar from '../../common/navBar'
    export default {
        name: "card",
        components:{
            navBar
        },
        data(){
            return {
                value:"",
                date:"",
                cards:[
                    '五星好评8折券',
                    '停车超过5小时支付8折券',
                    '充值满100赠20优惠券',
                    '晚间停车无门槛10元优惠券'
                ],

            }
        }
    }
</script>

<style scoped>
.card-info{
    width:800px;
    position:relative;
    margin-left:50%;
    left:-400px;
    margin-top:100px;
}
    .mycard{
        background-color: #f6f7fb;
        height:100px;
        line-height: 100px;
        position:relative;
    }
    li{
        display: inline-block;
        width:200px;
        margin-right:20px;
    }
    .myswitch{
        position:absolute;
        left:10px;
    }
</style>


