<template>
    <div class="addParkPot">
        <el-form  class="park-pot"  >
        <el-form-item>
            <el-input  v-model="map" style="width:200px" placeholder="地址"></el-input>
        </el-form-item>
        <el-form-item>
            <el-input style="width:200px" placeholder="开放时间"></el-input>
        </el-form-item>
        <el-form-item>
            <el-input style="width:200px" placeholder="歇业时间"></el-input>
        </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="search()">搜索</el-button>
                <el-button type="primary"@click="add()">确认</el-button>
        </el-form-item>
        </el-form>
        <div id="map"></div>
    </div>
</template>

<script>
    import navBar from '../../common/navBar'

    export default {
        name: "addParkPot",
        components: {
            navBar
        },
        data() {
            return {
                map:"杭州电子科技大学"
            }
        },
        mounted(){
            this.search();
        },
        methods:{
            search:function(){
                // 百度地图API功能
                var map = new BMap.Map("map");
                var point = new BMap.Point(116.331398,39.897445);
                map.centerAndZoom(point,12);
                // 创建地址解析器实例
                var myGeo = new BMap.Geocoder();
                // 将地址解析结果显示在地图上,并调整地图视野
                myGeo.getPoint(this.map, function(point){
                    if (point) {
                        map.centerAndZoom(point, 16);
                        map.addOverlay(new BMap.Marker(point));
                    }else{
                        console.log("您选择地址没有解析到结果!");
                    }
                }, "杭州市");
            },
            add:function(){
                this.$router.push("parkPot")
            }
        }
    }
</script>

<style scoped>
.park-pot{
    position:absolute;
    z-index:2;
}
#map{
    width:100%;
    height:100%;
    position: absolute;
    z-index:1;
    top:0px;
}
</style>



