<template>
    <div class="user">
        <nav-bar></nav-bar>
        <div class="table-filter">
            <el-form  class="myself-info" >
                <el-form-item>
                    <el-input style="width:200px" placeholder="姓名/手机号码/用户名"></el-input>
                    <el-button type="primary">查找</el-button>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary"@click="exportDate()">报表导出</el-button>
                </el-form-item>
            </el-form>
        </div>
        <el-table class="user-table":data="tableData" stripe style="width: 100%" :header-cell-style="rowStyle" border>
            <el-table-column prop="name" label="姓名" ></el-table-column>
            <el-table-column prop="phone" label="手机号码" ></el-table-column>
            <el-table-column prop="userName" label="用户名"></el-table-column>
            <el-table-column label="操作" width="300">
                <template slot-scope="scope">
                    <el-button @click="detail()" type="primary"size="small">详细</el-button>
                </template>
            </el-table-column>
        </el-table>
        <footer>
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-sizes="[5, 10, 25, 50]"
                    :page-size="100"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="25">
            </el-pagination>
        </footer>
        <el-dialog title="车主所持车辆详细信息" :visible.sync="dialogVisible">
            <el-table class="user-table":data="userDetail" stripe style="width: 100%" :header-cell-style="rowStyle" border>
                <el-table-column prop="carNumber" label="车牌" ></el-table-column>
                <el-table-column prop="carName" label="型号" ></el-table-column>
                <el-table-column prop="carColor" label="颜色"></el-table-column>
            </el-table>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
            </div>
        </el-dialog>

    </div>
</template>

<script>
    const ExportJsonExcel = require('js-export-excel')
    import navBar from '../../common/navBar'
    export default {
        name: "user",
        components: {
            navBar
        },
        data() {
            return {
                dialogVisible: false,
                tableData: [{
                    "name":"黄碧芳",
                    "phone":"17764590729",
                    "userId":"f019c8674c1f489aaacaefc52fa86f4d",
                    "userName":"wym",
                    "userType":"0"
                }],
                userDetail:[{
                        "carColor":"黄",
                        "carId":"5bef1da3a6ed44b381c7b2bc0ad3db35",
                        "carName":"大众",
                        "carNumber":"浙A55555",
                        "userId":"f019c8674c1f489aaacaefc52fa86f4d"
                    },{
                        "carColor":"黄",
                        "carId":"5bef1da3a6ed44b381c7b2bc0ad3db35",
                        "carName":"大众",
                        "carNumber":"浙A55555",
                        "userId":"f019c8674c1f489aaacaefc52fa86f4d"
                    },{
                        "carColor":"黄",
                        "carId":"5bef1da3a6ed44b381c7b2bc0ad3db35",
                        "carName":"大众",
                        "carNumber":"浙A55555",
                        "userId":"f019c8674c1f489aaacaefc52fa86f4d"
                    }],
                currentPage: 1
            }
        },
        methods:{
            rowStyle:function(){
                return {
                    "text-align":"center!important",
                    "color":"black"
                }
            },
            handleSizeChange(val) {
                console.log(`每页 ${val} 条`);
            },
            handleCurrentChange(val) {
                console.log(`当前页: ${val}`);
            },
            gotoDetail:function(){
                this.$router.push('/parkSpace');
            },
            detail:function(){
                this.dialogVisible=true
            },
            exportDate:function(){
                var option={};
                option.fileName = 'excel'
                option.datas=[
                    {
                        sheetData:this.tableData,
                        sheetName:'sheet',
                        sheetFilter:['name','phone','userName'],
                        sheetHeader: ['姓名','手机号码','用户名']
                    }
                ];
                let  toExcel=new ExportJsonExcel(option);
                toExcel.saveExcel();

            }

        }
    }

</script>

<style scoped>
    .table-filter{
        width:100%;
        height:100px;
        text-align:left;
        line-height:100px;
        padding-left:20px;
        padding:20px;
    }
    footer{
        position:fixed;
        bottom:20px;
        width:100%;
    }

</style>


