<template>
    <div class="parkingSpace">
        <nav-bar></nav-bar>
        <div class="table-filter">
            <el-button type="primary" @click="add()">添加停车位</el-button>
        </div>
        <div class="parkPot">
            <el-row :gutter="20">
                <el-col :span="3" v-for="item in 20" :index="item">
                    <div class="grid-content">
                        <el-tooltip class="item" effect="dark" :content="item" placement="top">
                            <div class="seat" @click="del()"></div>
                        </el-tooltip>

                    </div>
                </el-col>
            </el-row>
        </div>
        <footer>
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage4"
                    :page-sizes="[100, 200, 300, 400]"
                    :page-size="100"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="400">
            </el-pagination>
        </footer>
        <el-dialog :title="dialogTitle" :visible.sync="dialogVisible" >
            <el-form  class="park-pot"  >
                <el-form-item>
                    <el-input style="width:200px" placeholder="车位号"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-select v-model="status" placeholder="状态选择">
                        <el-option
                                v-for="item in options"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>

            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
            </div>
        </el-dialog>
        <el-dialog title="提示" :visible.sync="delDialogVisible" width="30%">
            <span>确认删除？删除后数据不可恢复</span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="delDialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="delDialogVisible = false">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
    import navBar from '../../../common/navBar'
    export default {
        name: "parkingSpace",
        components: {
            navBar
        },
        data() {
            return {
                dialogTitle:"添加停车场",
                dialogVisible: false,
                delDialogVisible: false,
                tableData: [{
                    "parkName": "杭电学生停车场",
                    "parkAddress": "使用中",
                    "parkDetail": " 杭电停车场 ",
                    "openTime": "06:00:00",
                    "closeTime": "12:00:00",
                    "longitude": "120.19",
                    "latitude": "30.26"
                }],
                options: [{
                    value: '1',
                    label: '未使用'
                }, {
                    value: '2',
                    label: '使用中'
                }, {
                    value: '3',
                    label: '已预约'
                }],
                status:"",

                currentPage4: 4
            }
        },
        methods:{
            rowStyle:function(){
                return {
                    "text-align":"center!important",
                    "color":"black"
                }
            },
            handleSizeChange(val) {
                console.log(`每页 ${val} 条`);
            },
            handleCurrentChange(val) {
                console.log(`当前页: ${val}`);
            },
            add:function(){
                this.dialogVisible =true;
                this.dialogTitle="添加停车位";

            },
            del:function(){
                this.delDialogVisible=true;
            },
            change:function(){
                this.dialogVisible =true;
                this.dialogTitle="更改停车位";

            },
            detail:function(){
                this.$router.push('/parkSpace');
            },
        }
    }
</script>

<style scoped>
    .table-filter{
        width:100%;
        height:100px;
        text-align:left;
        line-height:100px;
        margin-left:20px;
    }
    footer{
        position:fixed;
        bottom:20px;
        width:100%;
    }
    .seat {
        width: 50px;
        height: 50px;
        background-size: cover;
        background-image: url("../../../../images/park_seat.png");
    }
</style>


