<template>
    <div class="parkPot">
        <nav-bar active="parkPot"></nav-bar>
        <div class="table-filter">
            <el-button type="primary" @click="add()">添加停车场</el-button>
        </div>
        <el-table :data="tableData" stripe style="width: 100%" :header-cell-style="rowStyle" border>
            <el-table-column prop="parkName" label="停车场名称" ></el-table-column>
            <el-table-column prop="parkAddress" label="地址" ></el-table-column>
            <el-table-column prop="parkDetail" label="详细"></el-table-column>
            <el-table-column prop="openTime" label="开放时间"></el-table-column>
            <el-table-column prop="closeTime" label="歇业时间"></el-table-column>
            <el-table-column prop="longitude" label="经度"></el-table-column>
            <el-table-column prop="latitude" label="纬度"></el-table-column>
            <el-table-column label="操作" width="300">
                <template slot-scope="scope">
                    <el-button type="primary" size="small" @click="detail()">详细</el-button>
                    <el-button size="small" type="warning"@click="change()">修改</el-button>
                    <el-button size="small" type="danger"@click="del()">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <footer>
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-sizes="[5, 10, 25, 50]"
                    :page-size="100"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="100">
            </el-pagination>
        </footer>
        <el-dialog title="修改停车场" :visible.sync="changeDialogVisible" >
            <el-form  class="park-pot"  >
                <el-form-item>
                    <el-input style="width:200px" placeholder="详细"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-input style="width:200px" placeholder="开放时间"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-input style="width:200px" placeholder="歇业时间"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
            </div>
        </el-dialog>
        <el-dialog title="提示" :visible.sync="delDialogVisible" width="30%">
            <span>确认删除？删除后数据不可恢复</span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="delDialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="delDialogVisible = false">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
    import navBar from '../../common/navBar'

    export default {
        name: "parkPot",
        components: {
            navBar
        },
        data() {
            return {
                map:"",
                dialogVisible: false,
                changeDialogVisible: false,
                delDialogVisible: false,
                tableData: [{
                    "parkName": "杭电学生停车场",
                    "parkAddress": "杭州电子科技大学",
                    "parkDetail": " 杭电停车场 ",
                    "openTime": "06:00:00",
                    "closeTime": "12:00:00",
                    "longitude": "120.19",
                    "latitude": "30.26"
                }],
                currentPage: 1
            }
        },
        methods:{
            rowStyle:function(){
                return {
                    "text-align":"center!important",
                    "color":"black"
                }
            },
            handleSizeChange(val) {
                console.log(`每页 ${val} 条`);
            },
            handleCurrentChange(val) {
                console.log(`当前页: ${val}`);
            },

            add:function(){
                this.$router.push("addParkPot")


            },
            del:function(){
                this.delDialogVisible=true;
            },
            change:function(){
                this.dialogVisible =true;


            },
            detail:function(){
                this.$router.push('/parkSpace');
            },
        }
    }
</script>

<style scoped>
    .table-filter{
        width:100%;
        height:100px;
        text-align:left;
        line-height:100px;
        margin-left:20px;
    }
    footer{
        position:fixed;
        bottom:20px;
        width:100%;
    }

    #map{
        width:400px;
        height:400px;
    }

</style>


