<template>
    <div class="parkPot">
        <nav-bar active="parkPot"></nav-bar>
        <div class="table-filter">
            <el-button type="primary" @click="add()">添加收费策略</el-button>
        </div>
        <el-table :data="tableData" stripe style="width: 100%" :header-cell-style="rowStyle" border>
            <el-table-column prop="parkName" label="停车场名称" ></el-table-column>
            <el-table-column prop="oneHour" label="1小时" ></el-table-column>
            <el-table-column prop="threeHour" label="3小时"></el-table-column>
            <el-table-column prop="fiveHour" label="5小时"></el-table-column>
            <el-table-column prop="capping" label="封顶"></el-table-column>
            <el-table-column label="操作" width="300">
                <template slot-scope="scope">
                    <el-button size="small" type="warning"@click="change()">修改</el-button>
                </template>
            </el-table-column>
        </el-table>
        <footer>
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage4"
                    :page-sizes="[100, 200, 300, 400]"
                    :page-size="100"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="400">
            </el-pagination>
        </footer>
        <el-dialog title="添加收费策略" :visible.sync="dialogVisible" >
            <el-form  class="park-pot"  >
                <el-form-item >
                    <el-input style="width:200px" placeholder="停车场名称"></el-input>
                    <el-button type="primary">搜索</el-button>
                </el-form-item>
                <el-form-item>
                    <el-input style="width:200px" placeholder="1小时费用"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-input style="width:200px" placeholder="3小时费用"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-input style="width:200px" placeholder="5小时费用"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-input style="width:200px" placeholder="封顶"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
            </div>
        </el-dialog>
        <el-dialog title="修改收费策略" :visible.sync="changeDialogVisible" >
            <el-form  class="park-pot"  >
                <el-form-item>
                    <el-input style="width:200px" placeholder="1小时费用"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-input style="width:200px" placeholder="3小时费用"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-input style="width:200px" placeholder="5小时费用"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-input style="width:200px" placeholder="封顶"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="changeDialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="changeDialogVisible = false">确 定</el-button>
            </div>
        </el-dialog>
        <el-dialog title="提示" :visible.sync="delDialogVisible" width="30%">
            <span>确认删除？删除后数据不可恢复</span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="delDialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="delDialogVisible = false">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
    import navBar from '../../common/navBar'

    export default {
        name: "parkPot",
        components: {
            navBar
        },
        data() {
            return {
                dialogTitle:"添加收费策略",
                dialogVisible: false,
                changeDialogVisible: false,
                delDialogVisible: false,
                tableData: [{
                    "parkName":"杭电停车场",
                    "oneHour":"2",
                    "threeHour":"6",
                    "fiveHour":"10",
                    "capping":"16"
                }],
                options: [{
                    value: '1',
                    label: '未使用'
                }, {
                    value: '2',
                    label: '使用中'
                }, {
                    value: '3',
                    label: '已预约'
                }],
                status:"",
                currentPage4: 4
            }
        },
        methods:{
            rowStyle:function(){
                return {
                    "text-align":"center!important",
                    "color":"black"
                }
            },
            handleSizeChange(val) {
                console.log(`每页 ${val} 条`);
            },
            handleCurrentChange(val) {
                console.log(`当前页: ${val}`);
            },
            add:function(){
                this.dialogVisible =true;
                this.dialogTitle="添加收费策略";

            },
            del:function(){
                this.delDialogVisible=true;
            },
            change:function(){
                this.dialogVisible =true;
                this.dialogTitle="修改收费策略";

            }
        }
    }
</script>

<style scoped>
    .table-filter{
        width:100%;
        height:100px;
        text-align:left;
        line-height:100px;
        margin-left:20px;
    }
    footer{
        position:fixed;
        bottom:20px;
        width:100%;
    }

</style>


