<template>
    <div class="parkPot">
        <nav-bar active="parkPot"></nav-bar>
        <div class="table-filter">
            <el-form  class="myself-info"  >
                <el-form-item>
                    <el-date-picker
                            v-model="date"
                            type="daterange"
                            range-separator="至"
                            start-placeholder="开始日期"
                            end-placeholder="结束日期">
                    </el-date-picker>
                    <el-button type="primary">搜索</el-button>
                </el-form-item>
                <el-form-item>
                    <el-input style="width:200px" placeholder="停车场名称"></el-input>
                    <el-button type="primary">搜索</el-button>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary"@click="exportDate()">报表导出</el-button>
                </el-form-item>
            </el-form>
        </div>
        <el-table :data="tableData" stripe style="width: 100%" :header-cell-style="rowStyle" border>
            <el-table-column prop="date" label="日期"></el-table-column>
            <el-table-column prop="num" label="停车场名称" ></el-table-column>
            <el-table-column prop="num" label="停车人数" ></el-table-column>
            <el-table-column prop="time" label="停车时长"></el-table-column>
            <el-table-column prop="money" label="消费金额"></el-table-column>
            <el-table-column label="操作" width="300">
                <template slot-scope="scope">
                    <el-button type="primary" size="small" @click="detail()">详细</el-button>
                </template>
            </el-table-column>
        </el-table>
        <footer>
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-sizes="[100, 200, 300, 400]"
                    :page-size="100"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="400">
            </el-pagination>
        </footer>
        <div id="charts"></div>
        <el-dialog title="当日收入报表" :visible.sync="dialogVisible">
            <el-table class="user-table":data="profitDetail" stripe style="width: 100%" :header-cell-style="rowStyle" border>
                <el-table-column prop="time" label="时间" ></el-table-column>
                <el-table-column prop="user" label="用户名" ></el-table-column>
                <el-table-column prop="spend" label="停车时长"></el-table-column>
                <el-table-column prop="money" label="消费金额"></el-table-column>
            </el-table>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
            </div>
        </el-dialog>

    </div>
</template>

<script>
    import echarts from 'echarts'
    import 'echarts/lib/chart/line'
    import 'echarts/lib/component/tooltip'
    import 'echarts/lib/component/title'
    const ExportJsonExcel = require('js-export-excel')
    import navBar from '../../common/navBar'
    export default {
        name: "profit",
        components: {
            navBar
        },
        data() {
            return {
                dialogVisible:false,
                date:"",
                tableData: [{
                    "date": "2018-05-23",
                    "num": "15人",
                    "time": "25小时",
                    "money": "100元"
                }],
                profitDetail:[
                    {
                        time:"2018-05-24 15:17:15",
                        user:"小蛋黄",
                        spend:"2小时",
                        money:"5元"
                    }
                ],
                currentPage: 1,
                option :{
                    title : {
                        text: '月收入报表',
                        subtext: '纯属虚构'
                    },
                    tooltip : {
                        trigger: 'axis'
                    },
                    legend: {
                        data:['月收入']
                    },
                    toolbox: {
                        show : true,
                        feature : {
                            mark : {show: true},
                            dataView : {show: true, readOnly: false},
                            magicType : {show: true, type: ['line', 'bar']},
                            restore : {show: true},
                            saveAsImage : {show: true}
                        }
                    },
                    calculable : true,
                    xAxis : [
                        {
                            type : 'category',
                            boundaryGap : false,
                            data : ['杭电停车场','理工','下沙','滨江']
                        }
                    ],
                    yAxis : [
                        {
                            type : 'value',
                            axisLabel : {
                                formatter: '{value} 元'
                            }
                        }
                    ],
                    series : [
                        {
                            name:'月收入',
                            type:'line',
                            data:[11, 11, 15, 13, 12, 13, 10],
                            markPoint : {
                                data : [
                                    {type : 'max', name: '最大值'},
                                    {type : 'min', name: '最小值'}
                                ]
                            },
                            markLine : {
                                data : [
                                    {type : 'average', name: '平均值'}
                                ]
                            }
                        },
                    ]
                }
            }
        },
        mounted(){

             this.myChart=echarts.init(document.getElementById('charts'))
            this.myChart.setOption(this.option)
        },
        methods:{
            rowStyle:function(){
                return {
                    "text-align":"center!important",
                    "color":"black"
                }
            },
            handleSizeChange(val) {
                console.log(`每页 ${val} 条`);
            },
            handleCurrentChange(val) {
                console.log(`当前页: ${val}`);
            },
            detail:function(){
                this.dialogVisible=true;
            },
            exportDate:function(){
                var option={};
                option.fileName = 'excel'
                option.datas=[
                    {
                        sheetData:this.tableData,
                        sheetName:'sheet',
                        sheetFilter:['date','num','time','money'],
                        sheetHeader: ['日期','停车人数','停车总时间','总金额']
                    }
                ];
                let  toExcel=new ExportJsonExcel(option);
                toExcel.saveExcel();

            }
        }
    }
</script>

<style scoped>
    .table-filter{
        width:100%;
        height:100px;
        text-align:left;
        line-height:100px;
        padding:20px;
    }

    #charts{
        width:100%;
        height:400px;
    }

</style>


